@import (reference) 'common/color.less';
.class-box{margin:0 auto;margin-top:50px;}
.content{width:100%;margin-top:33px;}
.contentnews{
  width:878px;
  margin-top:40px;
  height:100%;
  border:1px dotted #d5d9d1;
  position:relative;
  padding-right:65px;
  padding-bottom:30px;
}
.edittext{width:98px;height:24px;display:inline-block}
.rimg{position:absolute;right:-1px;top:-1px;z-index:2}
.headbg{height:40px;line-height:40px;background:@list-bg;}
.title{
  width:841px;
  height:50px;
  margin:45px auto;
  color:white;
  margin-bottom:42px;
  background:url('../../bundle/img/butterfly.png') no-repeat center center;
  line-height:40px;
}
.contentbox{width:605px;color:@black;}
.teacher,.student{
  display:inline-block;
  width:20px;
  height:22px;
  background: url("../../bundle/img/icon.png") 0 0 no-repeat;
  vertical-align:middle;
}
.teacher{background-position:-175px -62px;}
.student{background-position:-445px -61px}
.contentbox .teacherbox span,.contentbox .studentbox span{display:block;float:left;width:100.7px;}

